import React, { Component } from "react";
import "./rq.css";
import $ from "jquery";
import PropTypes from "prop-types";

class RandomQuote extends Component {
  constructor(props) {
    super(props);

    this.encodeQuoteData = this.encodeQuoteData.bind(this);
    this.getQuote = this.getQuote.bind(this);
    this.constructTwitterIconHref = this.constructTwitterIconHref.bind(this);

    this.state = {
      quoteText: "Quality means doing it right when no one is looking.",
      quoteAuthor: "Henry Ford",
      twitterIconHref: ""
    };
  }

  constructTwitterIconHref() {
    this.setState({
      twitterIconHref:
        this.props.twitterIconHref +
        encodeURIComponent(`${this.state.quoteText} ${this.state.quoteAuthor}`)
    });
  }

  encodeQuoteData() {
    return encodeURIComponent(
      `${this.state.quoteText} ${this.state.quoteAuthor}`
    );
  }

  getQuote() {
    $.getJSON(
      this.props.url,
      ({ quoteText, quoteAuthor }) => {
        this.setState({
          quoteText,
          quoteAuthor
        });
      },
      "jsonp"
    );
  }

  render() {
    return (
      <div className="bottom example-twitter">
        <Quote
          quoteText={this.state.quoteText}
          quoteAuthor={this.state.quoteAuthor}
        />
        <Button getQuote={this.getQuote} />
      </div>
    );
  }
}

const Quote = ({ quoteText, quoteAuthor = "Unknown" }) => (
  <blockquote className="blockquote text-center quote-text">
    <p className="mb-0">{quoteText}</p>
    <footer className="blockquote-footer">
      <span>{"Me"}</span>
    </footer>
  </blockquote>
);

const Button = ({ getQuote }) => (
  <button className="btn btn-outline-secondary" onClick={getQuote}>
    Next
  </button>
);

Button.propTypes = {
  getQuote: PropTypes.func.isRequired
};

RandomQuote.defaultProps = {
  url:
    "https://api.forismatic.com/api/1.0/?method=getQuote&key=457653&format=jsonp&lang=en&jsonp=?",
  twitterIconHref:
    "https://twitter.com/intent/tweet?hashtags=quotes&related=freecodecamp&text="
};

RandomQuote.propTypes = {
  url: PropTypes.string.isRequired,
  twitterIconHref: PropTypes.string.isRequired
};

Quote.propTypes = {
  quoteText: PropTypes.string.isRequired,
  quoteAuthor: PropTypes.string.isRequired
};

export default RandomQuote;
